import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Edition } from "../../../data";
import * as styles from "./rules.module.css";

type RulesProps = {
  edition: Edition;
};

const Rules: React.FC<RulesProps> = ({ edition }) => {
  const { t } = useTranslation();
  const sections = t("sections", { returnObjects: true }) as any[];

  return (
    <div className={"page"} id={"rules"}>
      <h1 className={`title ${styles.title}`}>{t("title")}</h1>

      <ol className={`${styles.ol} ${styles.ruleList}`}>
        {sections.map((item, index) => (
          <li key={"ruleSection" + index} className={styles.li}>
            {item.title}
            <ol className={styles.ol}>
              {item.details.map((details: string, index2: number) => (
                <li
                  key={"ruleDetails" + index + index2}
                  className={`${styles.li} p`}
                >
                  {details}
                </li>
              ))}
            </ol>
          </li>
        ))}
      </ol>
    </div>
  );
};

export default Rules;
