import React from "react";
import { PageProps, graphql } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";

import Layout from "../layout/layout";
import Seo from "../layout/seo/seo";
import Rules from "../components/rules/pages/rules";
import { currentEdition } from "../data";

const RulesPage: React.FC<PageProps> = ({ location }) => {
  const { t } = useTranslation();

  return (
    <Layout>
      <Seo title={t("title")} pathname={location.pathname} />

      <Rules edition={currentEdition} />
    </Layout>
  );
};

export default RulesPage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: { ns: { in: ["common", "rules"] }, language: { eq: $language } }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
